import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

function initWs(){
  let mytype = {"type":10090};
  let typeString = JSON.stringify(mytype);
  var websocket = new WebSocket("ws://114.80.110.63:34567")
  websocket.onopen = function (evt) {
    websocket.send(typeString)
  }
  websocket.onmessage = function (evt) {
    state.wsEvt = evt
    console.log(evt)
  } 
  return websocket;
}

const state = {
  ws: initWs(), 
  wsEvt: {},
  wwjtoken: null,
  userId: null
}

const mutations = {
  onopen: (state) => {
    if(state.ws != undefined)
      return
    state.ws = new WebSocket("ws://114.80.110.63:34567")
    state.ws.onopen = function (evt) {
      state.ws.send(typeString)
    }
    state.ws.onmessage = function (evt) {
      state.wsEvt = evt
    } 
  },
  send: (state, str) => {
    state.ws.send(str)
  },
  saveWwjtoken: (state, token) => {
    state.wwjtoken = token
  },
  saveUserId: (state, userId) => {
    state.userId = userId
  }
}

const actions = {
  onopen: (context) => {
    context.commit('onopen')
  },
  send: (context, str) => {
    context.commit('send', str)
  },
  saveWwjtoken: (context, token) => {
    context.commit('saveWwjtoken', token)
  },
  saveUserId: (context, userId) => {
    context.commit('saveUserId', userId)
  }
}

export default new Vuex.Store({
  state,
  actions,
  mutations
})


