// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App.vue'
import router from './router/index.js'
import Flexible from './assets/js/flexible.js'
// import JQuery from 'jquery'
import layer from 'vue-layer'
import {Alert, Confirm, Toast} from 'wc-messagebox'
import 'wc-messagebox/style.css'
import VueLazyload from 'vue-lazyload'
import store from './store'
// import VueAwesomeSwiper from 'vue-awesome-swiper'
// Vue.use(VueAwesomeSwiper)
 // import WSAvcPlayer from '../static/js/http-live-player.js'
// require('es6-promise').polyfill()
Vue.use(Alert, {});
Vue.use(Confirm, {});
Vue.use(Toast);
Vue.use(VueLazyload, {
  preLoad: 1,    //预加载高度的比例
  error: '../static/wawa_03.png',  //图像的src加载失败
  loading: '../static/wawa_03.png', //src的图像加载
  attempt: 1,  //尝试计数
  listenEvents: [ 'scroll', 'mousewheel' ] //你想要监听的事件,我个人喜欢全部监听，方便
});
 // let Base64 = require('js-base64').Base64;
// import "babel-polyfill";
// import VueAreaLinkage from 'vue-area-linkage'
const isDebug_mode = process.env.NODE_ENV !== 'production';
Vue.config.debug = isDebug_mode;
Vue.config.devtools = isDebug_mode;
Vue.config.productionTip = isDebug_mode;

Vue.prototype.$layer = layer(Vue);
Vue.prototype.Flexible = Flexible;
// Vue.prototype.$ = JQuery;
Vue.config.productionTip = false;
//Vue.prototype.$http = axios;
// Vue.prototype.WSAvcPlayer = WSAvcPlayer;
// Vue.use(Navigation, {router})
// Vue.use(VueAreaLinkage)
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store, //注入到vue
  components: { App },
  template: '<App/>'
})

function getCookie(name) {
  var v = window.document.cookie.match('(^|;) ?' + name + '=([^;]*)(;|$)');
  return v ? v[2] : null;
}


const token = getCookie("wawajitoken");
router.beforeEach((to, from, next) => {
  $.get('../static/version.json?_=' + Math.random()).then(response => {//判断版本信息
    // console.log(response);
    // console.log(process.env.VERSION)
    if (200 == response.status) {
      if (process.env.VERSION !== response.version) {
         var message = "系统版本有更新，点击确认加载最新！";
        var cofirm = confirm(message)
        if(cofirm == true){
          window.location.reload(true);
        }else{
          window.location.reload(false);
        }
        // Vue.prototype.$alert(message, '系统提示', {
        //   confirmButtonText: '确定',
        //   callback: function(){
        //     window.location.reload(true);
        //   }
        // });

        return;
      }
      next();
    }
  }).catch(err => {
    console.error(err);
    next();
  });

  if (to.matched.some(record => record.meta.requireAuth)){ // 判断该路由是否需要登录权限
      if (token) { // 判断当前的token是否存在
        next();
      }
      else {
        next({
          path: '/visit/login',
          query: {redirect: to.fullPath} // 将跳转的路由path作为参数，登录成功后跳转到该路由
        })
    }

  }
  else {
    next();
  }

});
var urlhost = process.env.API_AJAX;
var url = window.location.href.split("#")[0];
$.ajax({
  type: 'POST',
  url: urlhost+"share/index",
  data$Type: "jsonp",
  data:{
    url:url
  },
  success: function (data) {
    var mes = JSON.parse(data).wx_js_config;
    wx.config(JSON.parse(mes));
  },
});

wx.ready(function(){

  var muser_id = getCookie("muser_id");
  var user_id = getCookie("userId");
  var link = window.location.href.split("?")[0]+"?muser_id="+ muser_id+"&user_id="+ user_id;
  var shareData = {
    title: '疯狂抓抓抓',
    desc: '网红直播抓娃娃，抓到立即带回家',
    link: link,
    imgUrl: 'https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/108-108.png',
    success: function (res) {
      //alert('已分享');
    },
    cancel: function (res) {

    }
  };
  wx.onMenuShareTimeline(shareData);
  wx.onMenuShareAppMessage(shareData);
  wx.onMenuShareQQ(shareData);
  wx.onMenuShareWeibo(shareData);
  wx.onMenuShareQZone(shareData);
});



