import Vue from 'vue'
import Router from 'vue-router'
const HomePage = resolve => require(['@/components/home'], resolve);
const testPage = resolve => require(['@/components/test'], resolve);
const Login = resolve => require(['@/components/visit/login'], resolve);
const Register = resolve => require(['@/components/visit/register'], resolve);
const PhoneRegister = resolve => require(['@/components/visit/phoneregister'], resolve);
const NormalRegister = resolve => require(['@/components/visit/normalregister'], resolve);
const ForgetPassword = resolve => require(['@/components/visit/forgetpassword'], resolve);
const Bind = resolve => require(['@/components/visit/bind'], resolve);
const SetPassword = resolve => require(['@/components/visit/setpassword'], resolve);
const User = resolve => require(['@/components/usercenter/index'], resolve);
const Batai = resolve => require(['@/components/batai/index'], resolve);
const UserInfo = resolve => require(['@/components/usercenter/userInfo'], resolve);
const Born = resolve => require(['@/components/usercenter/gucang'], resolve);
const MyDoll = resolve => require(['@/components/usercenter/mydoll'], resolve);
const Mail = resolve => require(['@/components/usercenter/mail'], resolve);
const History = resolve => require(['@/components/usercenter/history'], resolve);
const MyDiamond = resolve => require(['@/components/usercenter/mydiamond'], resolve);
const myPoints = resolve => require(['@/components/usercenter/myPoints'], resolve);
const TheDetail = resolve => require(['@/components/usercenter/theDetail'], resolve);
const MailOrder = resolve => require(['@/components/usercenter/mailOrder'], resolve);
const OrderDetail = resolve => require(['@/components/usercenter/orderDetail'], resolve);
const AddRess = resolve => require(['@/components/usercenter/address'], resolve);
const NewAddRess = resolve => require(['@/components/usercenter/newAddress'], resolve);
const ChangAddress = resolve => require(['@/components/usercenter/changaddress'], resolve);
const PayRecords = resolve => require(['@/components/usercenter/payrecords'], resolve);
const Notice = resolve => require(['@/components/batai/notice'], resolve)
const PayResults = resolve => require(['@/components/usercenter/payresults'], resolve);
//五一活动
const Entrance = resolve => require(['@/components/activity/index'], resolve);
const ActivityHome = resolve => require(['@/components/activity/activityHome'], resolve);
const FriendsList = resolve => require(['@/components/activity/friends'], resolve);
const InviTation = resolve => require(['@/components/activity/invitation'], resolve);
const OtherHome = resolve => require(['@/components/activity/otherHome'], resolve);
//五一活动登录
const ActivityLogin = resolve => require(['@/components/visit/activityLogin'], resolve);
const ActivityRegister = resolve => require(['@/components/visit/activityRegister'], resolve);
const ActivityPhone = resolve => require(['@/components/visit/activityPhone'], resolve);
const ActivityForgetPassword = resolve => require(['@/components/visit/activityForgetPassword'], resolve);
Vue.use(Router);


export default new Router({
  // mode: 'history',
  routes: [
    {
      path:'/activity/otherHome',
      name: 'OtherHome',
      component: OtherHome,
      meta: {
        keepAlive: false,
      }
    },
    {
      path:'/visit/activityLogin',
      name: 'ActivityLogin',
      component: ActivityLogin,
      meta: {
        keepAlive: true,
      }
    },
    {
      path:'/visit/activityRegister',
      name: 'ActivityRegister',
      component: ActivityRegister,
      meta: {
        keepAlive: true,
      }
    }, 
    {
      path:'/visit/activityPhone',
      name: 'ActivityPhone',
      component: ActivityPhone,
      meta: {
        keepAlive: true,
      }
    }, 
    {
      path:'/visit/activityForgetPassword',
      name: 'ActivityForgetPassword',
      component: ActivityForgetPassword,
      meta: {
        keepAlive: true,
      }
    },
    {
      path:'/',
      name: 'HomePage',
      component: HomePage,
      meta: {
        keepAlive: true,
      }
    },
    {
      path:'/test',
      name: 'testPage',
      component: testPage,
      meta: {
        keepAlive: true,
      }
    },
    {
      path:'/roomInfo',
      name: 'HomePage',
      component: HomePage,
      meta: {
        keepAlive: true,
      }
    },
    {
      path:'/usercenter/gucang',
      name: 'gucang',
      component: Born,
      meta: {
        keepAlive: false,
      }
    },
    {
      path:'/usercenter/index',
      name: 'usercenter',
      component: User
    },
    {
      path:'/usercenter/userInfo',
      name: 'userInfo',
      component: UserInfo,
      meta: {
        requireAuth: true,
      }
    },
    {
      path:'/usercenter/payrecords',
      name: 'payrecords',
      component: PayRecords,
      meta: {
        requireAuth: true,
      }
    },
    {
      path:'/usercenter/mydoll',
      name: 'mydoll',
      component: MyDoll,
      meta: {
        requireAuth: true,
      }
    },
    {
      path:'/usercenter/mail',
      name: 'mail',
      component: Mail,
      meta: {
        requireAuth: false,
      }
    },
    {
      path:'/usercenter/history',
      name: 'mail',
      component: History
    },
    {
      path:'/usercenter/mydiamond',
      name: 'mail',
      component: MyDiamond,
      meta: {
        requireAuth: true,
      }
    },
    {
      path:'/usercenter/myPoints',
      name: 'mail',
      component: myPoints,
      meta: {
        requireAuth: true,
      }
    },
    {
      path:'/usercenter/payresults',
      name: 'payresults',
      component: PayResults,
      meta: {
        requireAuth: true,
      }
    },
    {
      path:'/usercenter/theDetail',
      name: 'mail',
      component: TheDetail
    },
    {
      path:'/usercenter/mailOrder',
      name: 'mail',
      component: MailOrder,
      meta: {
        requireAuth: true,
      }
    },
    {
      path:'/usercenter/orderDetail',
      name: 'mail',
      component: OrderDetail
    },
    {
      path:'/usercenter/address',
      name: 'address',
      component: AddRess,
      meta: {
        requireAuth: true,
      }
    },
    {
      path:'/usercenter/newAddress',
      name: 'mail',
      component: NewAddRess,
    },
    {
      path:'/usercenter/changaddress',
      name: 'changeAddress',
      component: ChangAddress
    },
    {
      path:'/visit/register',
      name: 'register',
      component: Register
    },
    {
      path:'/visit/phoneregister',
      name: 'PhoneRegister',
      component: PhoneRegister
    },
    {
      path:'/visit/normalregister',
      name: 'normalregister',
      component: NormalRegister
    },
    {
      path:'/visit/forgetpassword',
      name: 'forgetpassword',
      component: ForgetPassword
    },
    {
      path:'/visit/setpassword',
      name: 'setpassword',
      component: SetPassword
    },
    {
      path:'/visit/bind',
      name: 'bind',
      component: Bind,
      meta: {
        requireAuth: true,
      }
    },
    {
      path:'/visit/login',
      name: 'login',
      component: Login
    },
    {
      path:'/batai',
      name: 'batai',
      component: Batai,
      meta: {keepAlive: false}
    },
    {
      path:'/batai/notice',
      name: 'notice',
      component: Notice,
      meta: {
        requireAuth: true,
      }
    },
    {
      path: '/activity/index',
      component: Entrance,
      meta: {
        keepAlive: false,
      }
    },
    {
      path: '/activity/activityHome',
      component: ActivityHome,
      meta: {
        keepAlive: false,
      }
    },
    {
      path: '/activity/friends',
      component: FriendsList,
      meta: {
        keepAlive: false,
      }
    },
    {
      path: '/activity/invitation',
      component: InviTation,
      meta: {
        keepAlive: false,
      }
    },
    {
      path: '*',
      component: HomePage
    }
  ]
})



