<template>
  <div id="app">
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive"></router-view>
    </keep-alive>
    <router-view v-if="!$route.meta.keepAlive"></router-view>
  </div>
</template>
<style>
   @import "../static/commonStyle.css";
</style>
<script>
export default {
  name: 'App',
  data(){
    return{

    }
  },
  created(){
      document.body.removeChild(document.getElementById('appLoading'))
      setTimeout(function() {
        document.getElementById('app').style.display = 'block';
      }, 500)

  }

}
</script>






